<template>
  <div class="admin-buttons">
    <button @click="downloadExcel('uts')">Download UTS</button>
    <button @click="downloadExcel('uas')">Download UAS</button>
  </div>
</template>

<script setup>
const downloadExcel = (jenis) => {
  fetch(`https://a96b-103-158-126-212.ngrok-free.app/download-excel?jenis=${jenis}`, {
    headers: {
      "ngrok-skip-browser-warning": "true",
    },
  })
    .then((response) => response.blob())
    .then((blob) => {
      const url = window.URL.createObjectURL(blob);
      const a = document.createElement("a");
      a.href = url;
      a.download = `data_nilai_${jenis}.xlsx`;
      document.body.appendChild(a);
      a.click();
      a.remove();
      window.URL.revokeObjectURL(url);
    })
    .catch((err) => {
      console.error("Gagal mengunduh:", err);
      alert("Gagal mengunduh file. Pastikan server aktif dan Ngrok berjalan.");
    });
};
</script>

<style scoped>
button {
  background-color: #4f46e5; /* indigo */
  color: white;
  padding: 0.75rem 1.5rem;
  margin: 0.5rem;
  font-size: 1rem;
  font-weight: 600;
  border: none;
  border-radius: 0.5rem;
  cursor: pointer;
  box-shadow: 0 4px 14px rgba(79, 70, 229, 0.25);
  transition: background-color 0.3s ease, transform 0.2s ease;

  /* Center text inside button */
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

@media (max-width: 600px) {
  button {
    width: 100%;
    font-size: 0.95rem;
  }
}

.admin-buttons {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 1rem;
  padding: 2rem;
}
</style>
